.contact-head {
    margin-top: 20px;
    margin-left: 20px;
}
h3 {
    padding: 2px;
    font-family: monospace;
    font-size:x-large;
}

.form {
    font-family: monospace;
    font-size:x-large;
    padding: 20px;

    ::placeholder {
        font-family: monospace;
        font-size:x-large;
        padding: 5px;
        }
        
    .input-field {
        font-size: large;
        width: 35%;
        height: 40px;

    }
    .message-field {
        height: 150px;
        width: 35%;
        font-size: large;
        font-family: monospace;
    }
    .button {
        width: 35%;
        font-family: monospace;
        font-size: x-large;
        margin-top: 20px;
        background-color:#fcf2ec;
        color:  #355c7d;
        height: 50px;
        margin-bottom: 20px;;
    }
}
.error-text {

    margin-left: 20px;
    font-family: monospace;
    font-size: 1rem;
    margin-bottom: 10px;
}
a {
    text-decoration: none;
}